<template>
<div class="eventlistpage">
    <Headerbar></Headerbar>
    <br>
    <Searchbar @emitkeyword="searchkeyword" style="marginTop:70px" placehold="搜尋活動" width="328" height="43"></Searchbar>
    <div class="catawrap">
        <catatag :class="{tagclick:currenttagindex==index}" @typehandler="selecttype" v-for="(item,index) of taglist" :key="index" :tagname="item" :tagindex=index></catatag>
    </div>
    <div class="eventlist">
        <Actblockeventlist v-for="item of filtereventlist" :key="item[14]" :name="item[0]" :loc="item[3]" :date="item[1]" :time="item[2]" :actin="item[6]" :waitingnum="item[7]" :type="item[5]"></Actblockeventlist>
    </div>
</div>
</template>

<script>
import Headerbar from '../components/Header'
import Searchbar from '../components/Searchbar'
import catatag from '../components/catatag'
import Actblockeventlist from '../components/Actblock-eventlist'
import axios from 'axios'
export default {
    data() {
        return {
            taglist: ['親子', '手做', '戶外', '食物'],
            eventlist: [],
            keyword: '',
            currenttype: '',
            currenttagindex: '-1'
        }
    },
    components: {
        Headerbar,
        Searchbar,
        catatag,
        Actblockeventlist
    },
    created() {
        axios({
                method: 'get',
                url: 'https://script.google.com/macros/s/AKfycbwu0IumvrBT9W5XybuCu-_hQxn8r8i37EQ-ziiR0N3Mfjkilx2ww_NRS8kEtqNMKV_I/exec',
                params: {
                    mode: 'all',
                    id: '950ac145-5aa9-4456-b48a-03e0b2da05ff'
                }
            })
            .then((response) => {
                this.eventlist = response.data;
                console.log(response.data)
            })
    },
    methods: {
        searchkeyword(val) {
            this.keyword = val
            console.log(val)
        },
        selecttype(val, index) {
            
            if (this.currenttagindex == index) {
                this.currenttagindex = -1;
                this.currenttype = ''
            } else {
                this.currenttagindex = index
                this.currenttype = val
            }

        }
    },
    computed: {
        filtereventlist() {
            return this.eventlist.filter(item => {
                if (this.keyword == '' && this.currenttype == '') {
                    return this.eventlist
                } else {
                    //在種類是全部的時候，關鍵字搜尋
                    if (this.currenttype == '') {
                        if (item[0].search(this.keyword) != -1 && this.currenttype == item[5]) {
                            return item
                        }
                        //在關鍵字為空（全部），種類篩選
                    } else if (item[0].search(this.keyword) == -1) {
                        if (item[0].search(this.keyword) != -1 && this.currenttype == item[5]) {
                            return item
                        }

                    }
                    //在關鍵字篩選以及種類篩選的狀態下
                    else if (item[0].search(this.keyword) != -1 && this.currenttype == item[5]) {
                        return item
                    }

                }
                // else if(item[0].search(this.keyword) != -1){
                //     return item
                // }
                // else if(item[5]==this.currenttype&&item[0].search(this.keyword) != -1){
                //     return item
                // }
            })
        }
    }
}
</script>

<style lang="scss" scoped>
.tagclick {
    background-color: #67d89e;
    border: none;
    color: white;
}

.eventlistpage {
    .catawrap {
        padding-left: 23px;
        box-sizing: border-box;
        width: 100%;
        display: flex;
        overflow-y: hidden;
        overflow-x: scroll;
        white-space: nowrap;
        margin-bottom: 14px;
    }
}
</style>
